<template>
  <v-layout class="my-3">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>
    <v-container class="pa-0" dense>
      <v-form ref="form" class="mx-4">
        <v-row class="elevation-12" dense>
          <v-col xs12>
            <photographs-upload @file-upload="newfileUpload.photo = $event" />
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="4">
            Remarks
          </v-col>
          <v-col cols="4">
            <v-text-field v-model="newfileUpload.remarks" label="Remarks" dense></v-text-field>
          </v-col>
        </v-row>
        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="snackbar.color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
        <v-row>
          <v-btn primary v-if="isPostAuthorized" v-on:click="save" class="primary   m-2 subtitle-1"
            >Submit</v-btn
          >
        </v-row>
        <br />
      </v-form>
      <photo-card :url="apiUrl" :column-header="headerList" :href="href" />
    </v-container>
  </v-layout>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Login",
  data() {
    return {
      isFormLoading: false,
      headerList: ["photo", "remarks"],
      href: ["photo"],
      images: [],
      isPostAuthorized: true,
      apiUrl: `api/bids/infrastructure/building/${this.$route.params.regId}/photo`,
      records: [],
      files: [],
      // formData: new FormData(),
      houseownerinfo: [],
      newfileUpload: {
        id: 0,

        photo: "",
        remarks: ""
      },

      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      },

      user_role_id: localStorage.getItem("auth_user_role_id"),

      color: { color: "#3F3C3C" }
    };
  },
  computed: {
    isLoading() {
      if (this.isFormLoading) {
        return true;
      }
      return false;
    }
  },
  methods: {
    save() {
      this.isFormLoading = true;
      const formData = new FormData();
      const files = this.newfileUpload.photo;

      if (files.length > 0 && files[0].size < 1024 * 1024 * 5) {
        for (let i = 0; i < files.length; i += 1) {
          formData.append("photo[]", files[i], files[i].name);
        }
      }
      formData.append("remarks", this.newfileUpload.remarks);

      formData.append("_method", "POST");

      const saveUrl = `/api/bids/infrastructure/building/${this.$route.params.regId}/photo`;
      VueAxios.post(saveUrl, formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
        .then(response => {
          this.isFormLoading = false;
          //  debugger;
          if (response.status === 200 || response.status === 201) {
            this.houseownerinfo.push(response.data.data);
            // this.$toastr.s("All images uplaoded successfully");
            this.files = [];
            this.photo = [];
            this.snackbar.show = true;
            this.successfulMessage = "Record added successfully.";
            this.$refs.form.reset();
          }
        })
        .catch(error => {
          this.isFormLoading = false;
          this.snackbar.show = true;
          this.successfulMessage = "Record added successfully.";
          console.log(`api error:${error}`);
        });
    }
  }
};
</script>
<style scoped>
label span {
  color: red;
}
.card {
  padding: 15px;
}
</style>
