<template>
  <div>
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>
    <v-card class="sober d-flex flex-column">
      <v-toolbar color="primary" dark flat>
        <v-toolbar-title>
          Building Occupancy Information
        </v-toolbar-title>
      </v-toolbar>
      <v-form ref="form" class="mx-4" v-model="valid">
        <v-row>
          <v-col md="4">
            <label> <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-select
              v-model="newbuildingOccupancy.building_use_id"
              :rules="[requiredDropdown(' ')]"
              :items="buildingUse"
              item-value="id"
              item-text="building_use_type"
              :key="id"
              label="Select "
              dense
            ></v-select>
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label>  .<span>*</span></label>
          </v-col>

          <v-col md="4">
            <v-select
              v-model="buildingWardNo"
              :rules="[requiredDropdown(' ')]"
              label="Select ward"
              :items="wardName"
              item-value="id"
              item-text="ward_name"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row>
        <br />
        <br />
        <v-row>
          <v-col cols="4">
            <label>     (Night population)<span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newbuildingOccupancy.night_population"
              :rules="[
                required('    '),
                maxLength('    ', 10)
              ]"
              label="    "
              class="custom-text-field"
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <br />
        <br />
        <v-row>
          <v-col cols="4">
            <label>     (Day population)<span>*</span></label>
          </v-col>
          <v-col cols="4">
            <!-- <v-select v-model="newbuildingOccupancy.affiliated_consultancy" :items="supervisor" item-value="id" item-text="affiliated_consultancy_name" :key="id"   ></v-select> -->

            <v-text-field
              v-model="newbuildingOccupancy.day_population"
              :rules="[
                required('    '),
                maxLength('    ', 10)
              ]"
              label="     "
              class="custom-text-field"
              dense
            ></v-text-field>
          </v-col>
        </v-row>

        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="snackbar.color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
        <v-btn primary v-if="isPostAuthorized" v-on:click="save" class="primary" :disabled="!valid"
          >Submit</v-btn
        >
        <br />
      </v-form>
      <!-- <card :url="apiUrl" /> -->
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Supervisor",
  props: {
    nextStep: {
      type: Function
    },
    id: {
      type: [String, Number]
    }
  },
  data() {
    return {
      isFormLoading: false,
      isPostAuthorized: true,
      apiUrl: `api/registration/1/charkilla`,
      valid: false,
      maxLength(propertyType, maxLength) {
        return v =>
          (v && String(v).length <= maxLength) ||
          `${propertyType} must be less than ${maxLength} digits`;
      },
      required(propertyType) {
        return v => (v && String(v).length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      requiredNumber(propertyType) {
        return v => (v && v > 0) || `Only number is allowed`;
      },
      date: "",
      buildingOccupancy: [],
      buildingWardNo: "",
      ward_no: localStorage.getItem("ward_no"),
      is_municipal_staff: localStorage.getItem("is_municipal_staff"),
      buildingUse: [],
      userRoleId: localStorage.getItem("auth_user_role_id"),
      newbuildingOccupancy: {
        id: 0,
        bids_building_id: "",
        ward_no: "",
        building_use_id: "",
        night_population: "",
        day_population: ""
      },
      wardName: [],
      isValid: true,
      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      },
      records: ""
    };
  },
  computed: {
    isLoading() {
      if (
        this.buildingUse.length === 0 ||
        this.wardName.length === 0 ||
        this.buildingWardNo === 0 ||
        this.newbuildingOccupancy === 0
      ) {
        // all drop-down values not initiated from api
        return true;
      } else if (this.isFormLoading) {
        return true;
      }
      return false;
    }
  },
  created() {
    this.selectBoxApi();
    this.buildingInfoDetail();
    this.buildingOccupancyInfoDetail();
  },
  methods: {
    buildingInfoDetail() {
      const obj = this;
      // debugger;
      const url = `/api/bids/infrastructure/building/${this.$route.params.regId}`;
      VueAxios.get(url)
        .then(response => {
          obj.buildingWardNo = response.data.data.ward_no;
          // obj.selectWard(obj.wardName, obj.ward_no,obj.newgeneralInfo.ward_no);
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    buildingOccupancyInfoDetail() {
      const obj = this;
      const url = `/api/bids/infrastructure/building/${this.$route.params.regId}/occupancy`;
      VueAxios.get(url)
        .then(response => {
          //  debugger;
          obj.newbuildingOccupancy = response.data.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    selectBoxApi() {
      const obj = this;
      const buildingUseUrl = "api/bids/select-box/building-use";
      VueAxios.get(buildingUseUrl)
        .then(response => {
          obj.buildingUse = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
      // ward list
      const wardUrl = "api/bids/select-box/ward";
      VueAxios.get(wardUrl)
        .then(response => {
          const wardList = response.data;
          //debugger;
          obj.wardName = wardList;
          if (obj.is_municipal_staff == "0") {
            obj.wardName = wardList.filter(w => w.id == obj.ward_no);
            this.selectWard(wardList, obj.ward_no, obj.newgeneralInfo.ward_no);
          }
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    selectWard(wardList, selectedValue, element) {
      element = wardList.filter(w => w.id == selectedValue)[0];
    },
    save() {
      // debugger;
      const obj = this;
      obj.isFormLoading = true;

      if (
        obj.newbuildingOccupancy.id === "null" ||
        obj.newbuildingOccupancy.id === 0 ||
        obj.newbuildingOccupancy.id === undefined
      ) {
        const saveUrl = `/api/bids/infrastructure/building/${obj.$route.params.regId}/occupancy`;
        VueAxios.post(saveUrl, obj.newbuildingOccupancy)
          .then(response => {
            obj.isFormLoading = false;
            // debugger;
            if (response.status === 200 || response.status === 201) {
              obj.buildingOccupancy.push(response.data.data);
              obj.snackbar.show = true;
              obj.successfulMessage = "Successful.";
              obj.$refs.form.reset();
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
            obj.snackbar.show = true;
            obj.successfulMessage = "Save failed.";
            obj.$refs.form.reset();
          });
      } else {
        this.isFormLoading = true;
        const eurl = `/api/bids/infrastructure/building/${this.$route.params.regId}/occupancy/${this.newbuildingOccupancy.id}`;
        VueAxios.put(eurl, this.newbuildingOccupancy)
          .then(response => {
            // debugger;
            this.isFormLoading = false;
            if (response.status === 200 || response.status === 201) {
              this.snackbar.show = true;
              this.successfulMessage = "Data Updated successfully";
            }
            // else {
            //     this.$router.push({
            //         name: 'tabbed',
            //     });
            // }
            console.log(response);
          })
          .catch(error => {
            this.isFormLoading = false;
            console.log(error);
            obj.snackbar.show = true;
            obj.successfulMessage = "Save failed.";
            obj.$refs.form.reset();
          });
      }
    }
    // edit(id) {
    //   // debugger;
    //   //    const url = `api/registration/${this.$route.params.regId}/application/${this.$route.params.id}`;
    //   const url = `api/bids/infrastructure/building/${this.$route.params.regId}/occupancy/${id}`;
    //   VueAxios.get(url).then(response => {
    //     // debugger;
    //     this.newbuildingOccupancy = response.data.data;

    //     console.log(response.data.data);
    //   });
    // }
  }
};
</script>
<style scoped>
#styled-input {
  height: 30px;
  font-size: 20pt;
}

.styled-input label[for] {
  height: 30px;
  font-size: 20pt;
}

.row,
.col {
  height: 58px;
}

.custom-text-field.v-text-field.v-text-field--enclosed .v-input__slot {
  padding: 0;
}
label span {
  color: red;
}
.error-message {
  padding: 10px !important;
}
</style>
