<template>
  <div class="vld-parent">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>
    <v-card class="sober d-flex flex-column">
      <v-toolbar color="primary" dark flat>
        <v-toolbar-title>
          Architectural Information
        </v-toolbar-title>
      </v-toolbar>
      <v-form ref="form" class="mx-4" v-model="valid">
        <v-row>
          <v-col md="4">
            <label>   <span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newarchitecturalInfo.building_storey"
              :rules="[required(' '), maxLength(' ', 2)]"
              label=" "
              class="custom-text-field"
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label>  .<span>*</span></label>
          </v-col>

          <v-col md="4">
            <v-select
              v-model="buildingWardNo"
              :rules="[requiredDropdown(' ')]"
              label="Select ward"
              :items="wardName"
              item-value="id"
              item-text="ward_name"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row>
        <!-- <v-row>
          <v-col md="4">
            <label> Building Height Unit<span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="newarchitecturalInfo.building_height_unit_type_id"
              :rules="[requiredDropdown('Building Height Unit')]"
              :items="unitType"
              item-value="id"
              item-text="unit_type_en"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row> -->

        <v-row>
          <v-col md="4">
            <label>   <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newarchitecturalInfo.building_height"
              :rules="[required(' '), maxLength(' ', 3)]"
              label=" "
              class="custom-text-field"
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <!-- <v-row>
          <v-col md="4">
            <label> Building length Unit<span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="newarchitecturalInfo.building_length_unit_type_id"
              :rules="[requiredDropdown('Building length Unit')]"
              :items="unitType"
              item-value="id"
              item-text="unit_type_en"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row> -->
        <v-row>
          <v-col md="4">
            <label>  <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newarchitecturalInfo.building_length"
              :rules="[required(' '), maxLength(' ', 3)]"
              label=" "
              class="custom-text-field"
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label>   <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newarchitecturalInfo.building_max_area_sq_ft"
              :rules="[required(' '), maxLength(' ', 5)]"
              class="custom-text-field"
              label=" "
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <!-- <v-row>
          <v-col md="4">
            <label> Building breadth Unit<span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="newarchitecturalInfo.building_breadth_unit_type_id"
              :rules="[requiredDropdown('Building breadth Unit')]"
              :items="unitType"
              item-value="id"
              item-text="unit_type_en"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row> -->
        <v-row>
          <v-col md="4">
            <label>   <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newarchitecturalInfo.building_breadth"
              :rules="[required(' '), maxLength(' ', 5)]"
              label=" "
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <br />

        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="snackbar.color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
        <v-btn primary v-on:click="save" v-if="isPostAuthorized" class="primary" :disabled="!valid"
          >Submit</v-btn
        >
        <br />
      </v-form>
      <!-- <card :url="apiUrl" /> -->
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "architectural",
  props: {
    nextStep: {
      type: Function
    }
  },

  data() {
    return {
      isPostAuthorized: true,
      apiUrl: "",
      records: [],
      valid: false,
      isFormLoading: false,
      maxLength(propertyType, maxLength) {
        return v =>
          (v && String(v).length <= maxLength) ||
          `${propertyType} must be less than ${maxLength} digits`;
      },
      required(propertyType) {
        return v => (v && String(v).length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      requiredNumber(propertyType) {
        return v => (v && v > 0) || `Only number is allowed`;
      },
      date: "",
      ward_no: localStorage.getItem("ward_no"),
      is_municipal_staff: localStorage.getItem("is_municipal_staff"),

      error: "",
      districts: [],
      architecturalInfo: [],
      photo_name: "",
      formData: new FormData(),

      userRoleId: localStorage.getItem("auth_user_role_id"),
      architectural: [],
      name: false,
      unitType: [],
      nameNp: false,
      wardName: [],
      buildingWardNo: "",
      newarchitecturalInfo: {
        id: 0,
        bids_building_id: "1",
        ward_no: "",
        building_storey: "",
        building_length_unit_type_id: 1,
        building_length: "",
        building_breadth_unit_type_id: 1,
        building_breadth: "",
        building_height_unit_type_id: 1,
        building_height: "",
        building_max_area_sq_ft: ""
      },
      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      }
    };
  },

  computed: {
    isLoading() {
      if (this.wardName.length === 0 || this.unitType.length === 0) {
        return true;
      } else if (this.isFormLoading) {
        return true;
      }
      return false;
    }
  },
  created() {
    this.selectBoxApi();
    this.architecturalInfoDetail();
    this.buildingInfoDetail();
    this.selectWard();
  },
  methods: {
    selectBoxApi() {
      const obj = this;
      const wardUrl = "api/bids/select-box/ward";
      VueAxios.get(wardUrl)
        .then(response => {
          const wardList = response.data;
          //debugger;
          obj.wardName = wardList;
          if (obj.is_municipal_staff == "0") {
            obj.wardName = wardList.filter(w => w.id == obj.ward_no);
            this.selectWard(wardList, obj.ward_no, obj.newarchitecturalInfo.ward_no);
          }
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
      const unitTypeUrl = "api/bids/select-box/unit-type";
      VueAxios.get(unitTypeUrl)
        .then(response => {
          obj.unitType = response.data;
          // console.log(obj.buildingPermanent);
          // debugger;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    architecturalInfoDetail() {
      const obj = this;
      // debugger;
      const url = `/api/bids/infrastructure/building/${this.$route.params.regId}/architectural`;
      VueAxios.get(url)
        .then(response => {
          obj.newarchitecturalInfo = response.data.data;

          // obj.selectWard(obj.wardName, obj.ward_no,obj.newgeneralInfo.ward_no);
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },

    buildingInfoDetail() {
      const obj = this;
      // debugger;
      const url = `/api/bids/infrastructure/building/${this.$route.params.regId}`;
      VueAxios.get(url)
        .then(response => {
          obj.buildingWardNo = response.data.data.ward_no;
          // obj.selectWard(obj.wardName, obj.ward_no,obj.newgeneralInfo.ward_no);
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    selectWard(wardList, selectedValue, element) {
      element = wardList.filter(w => w.id == selectedValue)[0];
    },
    save() {
      this.isFormLoading = true;
      if (
        this.newarchitecturalInfo.id === "null" ||
        this.newarchitecturalInfo.id === 0 ||
        this.newarchitecturalInfo.id === undefined
      ) {
        const saveUrl = `/api/bids/infrastructure/building/${this.$route.params.regId}/architectural`;
        VueAxios.post(saveUrl, this.newarchitecturalInfo)
          .then(response => {
            // debugger;

            this.isFormLoading = false;
            if (response.status === 200 || response.status === 201) {
              this.architecturalInfo.push(response.data.data);
              this.snackbar.show = true;
              this.successfulMessage = "Successful.";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            this.snackbar.show = true;
            this.successfulMessage = "Save failed.";
            this.$refs.form.reset();
            console.log(`api error:${error}`);
          });
      } else {
        this.isFormLoading = true;
        const eurl = `/api/bids/infrastructure/building/${this.$route.params.regId}/architectural/${this.newarchitecturalInfo.id}`;
        VueAxios.put(eurl, this.newarchitecturalInfo)
          .then(response => {
            // debugger;
            this.isFormFLoading = false;
            if (response.status === 200 || response.status === 201) {
              this.snackbar.show = true;
              this.successfulMessage = "Data Updated successfully";
            }

            console.log(response);
          })
          .catch(error => {
            this.snackbar.show = true;
            this.successfulMessage = "Save failed.";

            console.log(error);
          });
      }
    }
    // edit(id) {
    //   // debugger;
    //   //    const url = `api/registration/${this.$route.params.regId}/application/${this.$route.params.id}`;
    //   const url = `api/bids/infrastructure/building/${this.$route.params.regId}/architectural/${id}`;
    //   VueAxios.get(url).then(response => {
    //     // debugger;
    //     this.architecturalInfo = response.data.data;
    //     console.log(response.data.data);
    //   });
    // }
  }
};
</script>
<style scoped>
#styled-input {
  height: 30px;
  font-size: 20pt;
}

.styled-input label[for] {
  height: 30px;
  font-size: 20pt;
}

.row,
.col {
  height: 58px;
}

.custom-text-field,
.v-text-field,
.v-text-field--enclosed,
.v-input__slot {
  padding: 0;
}
.v-text-field__details {
  margin: 0;
  padding: 0;
}
label span {
  color: red;
}
.error-message {
  padding: 10px !important;
}
</style>
