<template>
  <div>
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>

    <v-card class="sober d-flex flex-column">
      <v-toolbar color="primary" dark flat>
        <v-toolbar-title>
          Structural Information
        </v-toolbar-title>
      </v-toolbar>
      <v-form ref="form" class="mx-4" v-model="valid">
        <v-row>
          <v-col md="4">
            <label>  .<span>*</span></label>
          </v-col>

          <v-col md="4">
            <v-select
              v-model="buildingWardNo"
              :rules="[requiredDropdown(' ')]"
              label="Select ward"
              :items="wardName"
              item-value="id"
              item-text="ward_name"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label> <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-select
              v-model="newstructuralInfo.is_building_permanent"
              :rules="[requiredDropdown(' ')]"
              label="Select  "
              :items="buildingPermanent"
              item-value="id"
              item-text="building_permanent_type"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row>
        <br />
        <br />
        <v-row>
          <v-col cols="4">
            <label>    <span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="newstructuralInfo.building_category_id"
              :rules="[requiredDropdown('   ')]"
              label="Select    "
              :items="buildingCategory"
              item-value="id"
              item-text="building_category_type"
              :key="id"
              dense
            ></v-select>
          </v-col>
          <br />
        </v-row>
        <br />
        <v-row>
          <v-col cols="4">
            <label>  <span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="newstructuralInfo.building_structure_id"
              :rules="[requiredDropdown('  ')]"
              label="Select   "
              :items="buildingStructure"
              item-value="id"
              item-text="building_structure_type"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="4">
            <label>  <span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="newstructuralInfo.ceiling_material_type_id"
              :rules="[requiredDropdown(' ')]"
              label="Select  "
              :items="buildingCeilingMaterial"
              item-value="id"
              item-text="building_ceiling_material_type"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="4">
            <label>   <span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-radio-group
              dense
              v-model="newstructuralInfo.building_plan_compliance"
              :mandatory="true"
            >
              <v-radio label="/  (compliance)" v-bind:value="1"></v-radio>
              <v-radio label="  (Non-compliance)" v-bind:value="0"></v-radio>
            </v-radio-group>
          </v-col>
        </v-row>
        <br />
        <v-row>
          <v-col md="4">
            <label>
                

              <span>*</span></label
            >
          </v-col>
          <v-col md="4">
            <v-radio-group
              dense
              v-model="newstructuralInfo.vertical_discontinuity_compliance"
              :mandatory="true"
            >
              <v-radio label="/  (compliance)" v-bind:value="1"></v-radio>
              <v-radio label="  (Non-compliance)" v-bind:value="0"></v-radio>
            </v-radio-group>
          </v-col>
        </v-row>
        <br />
        <v-row hidden>
          <v-col cols="4">
            <label>     (Soft storey) <span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-radio-group
              dense
              v-model="newstructuralInfo.soft_storey_compliance"
              :mandatory="true"
            >
              <v-radio label=" (compliance)" v-bind:value="1"></v-radio>
              <v-radio label=" (Non-compliance)" v-bind:value="0"></v-radio>
            </v-radio-group>
          </v-col>
        </v-row>
        <br />
        <v-row>
          <v-col cols="4">
            <label>     (Setbacks)<span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-radio-group dense v-model="newstructuralInfo.setbacks_compliance" :mandatory="true">
              <v-radio label=" (compliance)" v-bind:value="1"></v-radio>
              <v-radio label=" (Non-compliance)" v-bind:value="0"></v-radio>
            </v-radio-group>
          </v-col>
        </v-row>
        <br />
        <v-row>
          <v-col cols="4">
            <label>  (Cantilever Projection)<span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-radio-group
              dense
              v-model="newstructuralInfo.cantilever_projection_compliance"
              :mandatory="true"
            >
              <v-radio label=" (compliance)" v-bind:value="1"></v-radio>

              <v-radio label=" (Non-compliance)" v-bind:value="0"></v-radio>
            </v-radio-group>
          </v-col>
        </v-row>

        <br />
        <v-row>
          <v-col md="4">
            <label>   (Narrow and tall)<span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-radio-group
              dense
              v-model="newstructuralInfo.overall_dim_ratio_compliance"
              :mandatory="true"
            >
              <v-radio label=" (compliance)" v-bind:value="1"></v-radio>
              <v-radio label=" (Non-compliance)" v-bind:value="0"></v-radio>
            </v-radio-group>
          </v-col>
        </v-row>
        <br />
        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="snackbar.color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
        <!-- <v-btn primary v-on:click="save" class="primary">Submit</v-btn> -->
        <v-btn primary v-on:click="save" v-if="isPostAuthorized" class="primary" :disabled="!valid"
          >Submit</v-btn
        ><br />
      </v-form>
      <!-- <card :url="apiUrl" /> -->
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Designer",
  props: {
    nextStep: {
      type: Function
    }
  },

  data() {
    return {
      isPostAuthorized: true,
      apiUrl: "",
      valid: false,
      isFormLoading: false,
      maxLength(propertyType, maxLength) {
        return v =>
          (v && String(v).length <= maxLength) ||
          `${propertyType} must be less than ${maxLength} digits`;
      },
      required(propertyType) {
        return v => (v && String(v).length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      requiredNumber(propertyType) {
        return v => (v && v > 0) || `Only number is allowed`;
      },
      date: "",
      roadCordinate: "",
      buildingWardNo: "",
      ward_no: localStorage.getItem("ward_no"),
      is_municipal_staff: localStorage.getItem("is_municipal_staff"),
      wardName: [],
      buildingPermanent: [],
      buildingCategory: [],
      buildingCeilingMaterial: [],
      buildingUse: [],
      buildingStructure: [],
      results: [],
      userRoleId: localStorage.getItem("auth_user_role_id"),
      newstructuralInfo: {
        id: 0,
        ward_no: "",
        is_building_permanent: "",
        building_category_id: "",
        building_structure_id: "",
        ceiling_material_type_id: "",
        building_plan_compliance: "",
        vertical_discontinuity_compliance: "",
        soft_storey_compliance: "",
        setbacks_compliance: "",
        cantilever_projection_compliance: "",
        overall_dim_ratio_compliance: ""
        //  isLoading: false
      },
      isValid: true,
      successfulMessage: "",
      structuralInfo: [],
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      },
      records: []
    };
  },
  computed: {
    isLoading() {
      if (
        this.buildingPermanent.length === 0 ||
        this.buildingStructure.length === 0 ||
        this.buildingCategory.length === 0 ||
        this.buildingCeilingMmaterial === 0 ||
        this.wardName.length === 0
      ) {
        // all drop-down values not initiated from api
        return true;
      } else if (this.isFormLoading) {
        return true;
      }
      return false;
    }
  },
  created() {
    this.selectBoxApi();
    this.structuralInfoDetail();
    this.buildingInfoDetail();
  },
  methods: {
    buildingInfoDetail() {
      const obj = this;
      // debugger;
      const url = `/api/bids/infrastructure/building/${this.$route.params.regId}`;
      VueAxios.get(url)
        .then(response => {
          obj.buildingWardNo = response.data.data.ward_no;
          // obj.selectWard(obj.wardName, obj.ward_no,obj.newgeneralInfo.ward_no);
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    structuralInfoDetail() {
      const obj = this;
      const url = `/api/bids/infrastructure/building/${this.$route.params.regId}/structural`;
      VueAxios.get(url)
        .then(response => {
          //  debugger;
          obj.newstructuralInfo = response.data.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    selectBoxApi() {
      const obj = this;

      const buildingPermanentUrl = "api/bids/select-box/building-permanent";
      VueAxios.get(buildingPermanentUrl)
        .then(response => {
          obj.buildingPermanent = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
      const wardUrl = "api/bids/select-box/ward";
      VueAxios.get(wardUrl)
        .then(response => {
          const wardList = response.data;
          //debugger;
          obj.wardName = wardList;
          if (obj.is_municipal_staff == "0") {
            obj.wardName = wardList.filter(w => w.id == obj.ward_no);
            this.selectWard(wardList, obj.ward_no, obj.newgeneralInfo.ward_no);
          }
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });

      const buildingUseUrl = "api/bids/select-box/building-use";
      VueAxios.get(buildingUseUrl)
        .then(response => {
          obj.buildingUse = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
      const buildingCategoryUrl = "api/bids/select-box/building-category";
      VueAxios.get(buildingCategoryUrl)
        .then(response => {
          obj.buildingCategory = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
      const buildingStructureUrl = "api/bids/select-box/building-structure";
      VueAxios.get(buildingStructureUrl)
        .then(response => {
          obj.buildingStructure = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
      const buildingCeilingMaterialUrl = "api/bids/select-box/building-ceiling-material";
      VueAxios.get(buildingCeilingMaterialUrl)
        .then(response => {
          obj.buildingCeilingMaterial = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    selectWard(wardList, selectedValue, element) {
      element = wardList.filter(w => w.id == selectedValue)[0];
    },
    save() {
      // debugger;
      this.isFormLoading = true;
      const obj = this;
      obj.isLoading = true;
      if (
        obj.newstructuralInfo.id === "null" ||
        obj.newstructuralInfo.id === 0 ||
        obj.newstructuralInfo.id === undefined
      ) {
        const saveUrl = `/api/bids/infrastructure/building/${obj.$route.params.regId}/structural`;
        VueAxios.post(saveUrl, obj.newstructuralInfo)
          .then(response => {
            obj.isFormLoading = false;
            //  debugger;
            if (response.status === 200 || response.status === 201) {
              obj.structuralInfo.push(response.data.data);
              obj.snackbar.show = true;
              // debugger;
              obj.successfulMessage = "Successful.";
              obj.$refs.form.reset();
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
          });
      } else {
        this.isFormLoading = true;
        const eurl = `/api/bids/infrastructure/building/${this.$route.params.regId}/structural/${this.newstructuralInfo.id}`;
        VueAxios.put(eurl, this.newstructuralInfo)
          .then(response => {
            this.isFormLoading = false;
            // debugger;
            if (response.status === 200 || response.status === 201) {
              this.snackbar.show = true;
              this.successfulMessage = "Data Updated successfully";
            }

            console.log(response);
          })
          .catch(error => {
            console.log(error);
          });
      }
    }
    // edit(id) {
    //   // debugger;
    //   //    const url = `api/registration/${this.$route.params.regId}/application/${this.$route.params.id}`;
    //   const url = `api/bids/infrastructure/building/${this.$route.params.regId}/structural/${id}`;
    //   VueAxios.get(url).then(response => {
    //     // debugger;
    //     this.structuralInfo = response.data.data;
    //     console.log(response.data.data);
    //   });
    // }
  }
};
</script>

<style scoped>
#styled-input {
  height: 30px;
  font-size: 20pt;
}

.styled-input label[for] {
  height: 30px;
  font-size: 20pt;
}

.row,
.col {
  height: 58px;
}

.custom-text-field.v-text-field.v-text-field--enclosed .v-input__slot {
  padding: 0;
}
label span {
  color: red;
}
.error-message {
  padding: 10px !important;
}
</style>
