<template>
  <div class="pa-10">
    <v-btn class="primary   m-2 subtitle-1" :to="{ name: 'inventory_tab' }">Back</v-btn>
    <v-stepper v-model="el" class="vstepper">
      <v-layout>
        <v-flex xs2>
          <v-stepper-header class="stepperHeader">
            <template v-for="n in steps" class="stepperHeaderTemplate">
              <v-card :key="n.sn" class="sober elevation-12 pt-1 pb-1">
                <v-stepper-step :key="n.sn" :step="n.sn" editable complete class="vstepperStep">
                  {{ n.text }}
                </v-stepper-step>
              </v-card>
            </template>
          </v-stepper-header>
        </v-flex>
        <v-flex xs10>
          <v-card>
            <v-stepper-items>
              <v-stepper-content v-for="n in steps" :key="`${n.sn}-content`" :step="n.sn" vertical>
                <component
                  class="sober"
                  :is="getComponent(n.sn)"
                  v-if="n.sn === el"
                  v-bind:next-step="nextStep"
                  v-bind:registration-id="regId"
                  :id="id"
                />
              </v-stepper-content>
            </v-stepper-items>
          </v-card>
        </v-flex>
        <!-- <v-flex xs6>
          <v-card> </v-card>
        </v-flex> -->
      </v-layout>
    </v-stepper>
  </div>
</template>
<script>
import GeneralInfo from "./components/GeneralInfo.vue";
import ArchitecturalInfo from "./components/ArchitecturalInfo.vue";
import StructuralInfo from "./components/StructuralInfo.vue";
import BuildingOccupancyInfo from "./components/BuildingOccupancyInfo.vue";
// import SocioeconomicInfo from "./components/SocioeconomicInfo.vue";
import FileUpload from "./components/FileUpload.vue";

const COMPONENT_MAP = {
  1: "GeneralInfo",
  2: "ArchitecturalInfo",
  3: "StructuralInfo",
  4: "BuildingOccupancyInfo",
  // 5: "SocioeconomicInfo",
  5: "FileUpload"
};

export default {
  name: "TabbedElements",
  props: {
    id: {
      cast: Number
    },
    regId: {
      cast: Number
    },
    sn: {
      cast: Number
    }
  },
  data() {
    return {
      el: Number(this.sn),
      userRoleId: localStorage.getItem("auth_user_role_id"),
      ward_no: localStorage.getItem("ward_no"),
      is_municipal_staff: localStorage.getItem("is_municipal_staff"),
      vertical: true,
      // : this.regId,
      // id: this.id,
      reg: [],
      steps: [
        { text: "General ", sn: 1 },
        { text: "Architectural ", sn: 2 },
        { text: "Structural ", sn: 3 },
        { text: "BuildingOccupancy ", sn: 4 },
        // { text: "Socioeconomic ", sn: 5 },
        { text: "Photographs Upload", sn: 5 }
      ]
      //  { text: '', sn: '11' },
      // { text: '', sn: '12' }],

      // el: 1,
      // steps: 10,
    };
  },
  components: {
    GeneralInfo,
    ArchitecturalInfo,
    StructuralInfo,
    BuildingOccupancyInfo,
    // SocioeconomicInfo,
    FileUpload
  },

  watch: {
    steps(val) {
      if (this.el > val) {
        this.el = val;
      }
    }
  },

  methods: {
    getComponent(n) {
      return COMPONENT_MAP[n];
    },
    nextStep(n) {
      // debugger;
      if (n === this.steps.sn) {
        this.el = 1;
      } else {
        this.el = n + 1;
      }
    }
  },
  mounted() {
    const urlApp = `api/bids/infrastructure/building/${this.$route.params.regId}`;
    this.$axios.get(urlApp).then(response => {
      this.reg = response.data.data;
    });
  }
};
</script>
<style scoped>
.vstepperStep {
  width: 280px;
  padding: 15px;
}
.vstepper {
  min-height: 100vh;
}
.stepperHeaderTemplate .stepperHeader {
  height: 10px;
  width: 300px;
}
</style>
